<template id="firstComponent">
    <div >
        <h1>I am a title.</h1>
        <p> written by {{ author }} </p>
        <div>
            <ul>
                <li v-for="list in lists">{{list}}</li>
            </ul>
        </div>
        <input v-model="addlist" type="text"/>
        <button @click='addList'>Add name</button>
        <div class="heightTest">
            <div :class="{'active':active}">
                
            </div>
            <button @click='changeH'>Change Height</button>
        </div>
    </div>

</template>

<script>
export default {
  data () {
      return {
          author: 'Jx',
          lists:['Jack','Rose','Tommy'],
          addlist:'',
          active:false
        }
    },
    methods:{
        addList() {
            this.lists.push(this.addlist);
            this.addlist=''
        },
        changeH() {
            this.active = true;
        }
    }

}
</script>
<!--style scoped  CSS   -->
<style scoped lang='scss'>
$blue : blue;

p{
    color: $blue;
}
.heightTest div {
    width:100%;
    height: 0;
    transition: all 0.5s;
    overflow: hidden;

    &.active {
        height:auto;
    }
}
</style>



